#speed-kit-analyzer .border-right {
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
  }

  #speed-kit-analyzer .border-left {
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
  }

  #speed-kit-analyzer .dark-green {
    color: #377401;
  }

  #speed-kit-analyzer .green {
    color: #8aae6c;
  }

  #speed-kit-analyzer .orange {
    color: #f2a541;
  }

  #speed-kit-analyzer .red {
    color: #f07368;
  }

  #speed-kit-analyzer .bg-dark-green {
    /* background-color: color(#377401 tint(15%)); */
    background-color: #c8e4b0;
  }

  #speed-kit-analyzer .bg-light-green {
    background-color: #f0f5eb;
  }

  #speed-kit-analyzer .bg-light-orange {
    background-color: #fef1e9;
  }

  #speed-kit-analyzer .bg-light-red {
    background-color: #feecec;
  }



