    }
  }

*/

/* Media Queries */

#speed-kit-analyzer .result__details-metrics {
    font-size: 16px;
  }

@media screen and (min-width: 30em) {

#speed-kit-analyzer .result__details-metrics {
      font-size: 16px
  }
    }

#speed-kit-analyzer .result__details-metrics .metricValue {
      font-weight: 400;
      font-size: 16px;
    }

@media screen and (min-width: 30em) {

#speed-kit-analyzer .result__details-metrics .metricValue {
        font-size: 18px
    }
      }

#speed-kit-analyzer .result__details-metrics .metricLabel {
      color: #7E7E7E;
      font-size: 12px;
    }

@media screen and (min-width: 30em) {

#speed-kit-analyzer .result__details-metrics .metricLabel {
        font-size: 14px
    }
      }

#speed-kit-analyzer .result__details-metrics .factor {
      color: #5cb85c;
      font-weight: 500;
      font-size: 16px;
    }

@media screen and (min-width: 30em) {

#speed-kit-analyzer .result__details-metrics .factor {
        font-size: 18px
    }
      }

#speed-kit-analyzer .result__details-toggle {
    color: rgb(25, 103, 190);
    font-size: 14px;
    cursor: pointer;
    font-weight: 400;
    position: relative;
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    background: #f8f8f8;
    border-radius: 50%;
    border: 1px solid #e8e8e8;
    -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, .1);
            box-shadow: 0 1px 4px rgba(0, 0, 0, .1);
    left: 50%;
    margin-left: -20px;
    margin-top: -38px;
    top: 0;
    padding: 5px;
  }

#speed-kit-analyzer .result__details-toggle svg {
      width: 100%;
      height: 100%;
      position: relative;
      display: block;
    }

#speed-kit-analyzer .result__details-toggle svg path {
        fill: rgb(90, 105, 115);
      }



